<template>
  <div id="app">

    <info-modal @close="closeModal" :popup="modal.show" :properties="modal.data" :title="modal.title">
    </info-modal>

    <h1 class="app-header"> Simple Market </h1>
    <router-view></router-view>

  </div>
</template>

<script>
import InfoModal from '@/components/InfoModal'

export default {
  name: 'app',

  data () {
    return {
      modal: this.$store.state.modal
    }
  },

  mounted: function () {
    // Store Contract Instance Address in config file
    this.$store.dispatch('initContractInstance', {address: '0xc5e0b79622146cacf856ffcd793cabdbf048a120'})
    this.$store.dispatch('initCoinbaseAddress')

    var marketInstance = this.$store.getters.getMarketContractInstance()
    if (marketInstance) {
      this.initMarketEventListeners(marketInstance)
    }
  },

  methods: {
    closeModal: function () {
      this.$store.dispatch(
        'refreshModal',
        {
          title: '',
          show: false,
          data: {}
        }
      )
    },

    initMarketEventListeners: function (instance) {
      // to fix event listeners from solidity
    }

  },

  watch: {

    '$store.state.modal': function (modalData) {
      this.modal = modalData
    },

    'store.state.contracts.marketInstance': function (instance) {
      this.initMarketEventListeners(instance)
    }

  },

  components: {
    InfoModal
  }

}
</script>

<style lang="scss">
#app {
  // Global  Styles
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;

  .app-header {
    border-bottom: 1px solid #ddd;
    padding: 15px;
    margin: 15px 15%;
    font-weight: bold;
    display: block;
    opacity: 0.8;
    font-size: 40px;
  }

  ul {
    padding: 0;
    list-style-type: none;
  }

  li {
    list-style: none;
  }

  .button {
    border: 1px solid #494e75; border-radius: 4px;
    padding: 5px 15px; color: #494e75;
    text-decoration: none;
    transition: all 0.3;

    &.left { border-radius: 0 5px 5px 0; }
    &.right { border-radius: 5px 0 0 5px; }
    &:focus { outline: none; }
    &:hover { background-color: #494e75; color: #fff; }
    &:active { background-color: #fff; color: #494e75; }
  }

  input, textarea {
    padding: 8px;
    font-size: 15px;
    resize: none; border: none;
    border-bottom: 1px solid #ddd;
    &:focus {
      outline: none;
      border-bottom: 1px solid #494e75;
    }

    &::-webkit-input-placeholder {
      transition: opacity 0.4s;
      opacity: 0.8;
    }
    &:focus::-webkit-input-placeholder {
      opacity: 0.2;
    }
  }
}
</style>



